.library {
  position: fixed;
  top: 0;
  left: 0;
  width: 20rem;
  height: 100%;
  background: white;
  box-shadow: 2px 2px 50px gray;
  overflow: scroll;
  transform: translateX(-100%);
  transition: all 0.5s ease;
  opacity: 0;
  h2 {
    padding: 2rem;
  }

  h1 {
    padding: 2rem;
  }
}

.library-song {
  display: flex;
  align-items: center;
  padding: 1rem 2rem 1rem 2rem;
  cursor: pointer;
  transition: background 0.1s ease;
  img {
    width: 30%;
  }
  &:hover {
    background: rgb(235, 226, 243);
  }
}

.song-description {
  padding-left: 1rem;
  h3 {
    font-size: 1.5rem;
  }
  h4 {
    padding-top: 0.1rem;
    font-size: 1rem;
  }
}

* {
  scrollbar-width: thin;
  scrollbar-color: rgba(155, 155, 155, 0.5) transparent;
}

* ::-webkit-scrollbar {
  width: 5px;
}

* ::-webkit-scrollbar-track {
  background: transparent;
}

* ::-webkit-scrollbar-thumb {
  background-color: hsla(0, 0%, 60.8%, 0.5);
  border-radius: 20px;
  border: transparent;
}

.selected {
  background: rgb(196, 185, 236);
}

.active-library {
  transform: translateX(0%);
  opacity: 1;
}

@media screen and (max-width: 768px) {
  .library {
    width: 100%;
  }
}
